import React from 'react';
import contacts from '../data/contacts-list';
import ContactsIcon from '../assets/img/contacts.svg';

const contactsToogle = () => {
  const buts = document.querySelectorAll('.contacts-popup__link');
  let timer = 0;
  if (buts[0].classList.contains('hidden')) {
    for (let i = buts.length - 1; i >= 0; i -= 1) {
      setTimeout(() => {
        buts[i].classList.toggle('hidden');
      }, (timer += 75));
    }
  } else {
    for (let i = 0; i < buts.length; i += 1) {
      setTimeout(() => {
        buts[i].classList.toggle('hidden');
      }, (timer += 75));
    }
  }
};

const ContactsPopup = () => (
  <div className="contacts-popup">
    {contacts.map(({ href, tooltip, icon }, i) => (
      <a
        key={String(i)}
        href={href}
        rel="noopener noreferrer"
        target="_blank"
        className="contacts-link contacts-popup__link hidden"
        tooltip={tooltip}
      >
        <i className={icon} />
      </a>
    ))}
    <button
      type="button"
      className="contacts-link contacts-button"
      onClick={contactsToogle}
      tooltip="Contacts"
    >
      <img src={ContactsIcon} alt="contacts" width="45" />
    </button>
  </div>
);

export default ContactsPopup;
