import React from 'react';
import contacts from '../data/contacts-list';

const ContactsPage = () => (
  <div className="contacts-page fullpage-centered">
    <div className="contacts">
      {contacts.map(({ href, icon }, i) => (
        <a
          key={String(i)}
          href={href}
          rel="noopener noreferrer"
          target="_blank"
        >
          <i className={icon} />
        </a>
      ))}
    </div>
  </div>
);

export default ContactsPage;
