import React from 'react';
import ReactIcon from '../assets/img/techonologies/react.svg';
import BootstrapIcon from '../assets/img/techonologies/bootstrap.svg';
import CssIcon from '../assets/img/techonologies/css.svg';
import HtmlIcon from '../assets/img/techonologies/html.svg';
import MySqlIcon from '../assets/img/techonologies/mysql.svg';
import NodeIcon from '../assets/img/techonologies/node.svg';
import PhpIcon from '../assets/img/techonologies/php.svg';

const generateImgComponent = (techIcon, techName, i) => (
  <img key={String(i)} src={techIcon} alt={techName} height="30" />
);
const TecnologyIcon = ({ technologies }) => technologies.map((tech, i) => {
  switch (tech) {
    case 'react':
      return generateImgComponent(ReactIcon, tech, i);
    case 'bootstrap':
      return generateImgComponent(BootstrapIcon, tech, i);
    case 'css':
      return generateImgComponent(CssIcon, tech, i);
    case 'html':
      return generateImgComponent(HtmlIcon, tech, i);
    case 'mysql':
      return generateImgComponent(MySqlIcon, tech, i);
    case 'node':
      return generateImgComponent(NodeIcon, tech, i);
    case 'php':
      return generateImgComponent(PhpIcon, tech, i);
    default:
      return '';
  }
});

export default TecnologyIcon;
