import React from 'react';
import PropTypes from 'prop-types';

import TecnologyIcon from '../utils/TecnologyIcon';

const Project = ({
  title, features, instruments, links, icons,
}) => (
  <div className="project">
    <h2 className="title">{title}</h2>
    <div className="feauters">
      <h3>Features</h3>
      <ul>
        {features.map((feature, i) => (
          <li key={String(i)}>{feature}</li>
        ))}
      </ul>
    </div>
    <div className="instruments">
      {instruments.map((instrument, i) => (
        <div key={String(i)} className="instrument-wrap">
          <p>{instrument.for}</p>
          <div className="multiple-instruments">
            {instrument.used.map((el, i) => (
              <span key={String(i)}>{el}</span>
            ))}
          </div>
        </div>
      ))}
    </div>
    <div className="links">
      {links.demo ? <a href={links.demo}>demo</a> : ''}
      {links.source ? <a href={links.source}>source</a> : ''}
    </div>
    <div className="icons">
      <TecnologyIcon technologies={icons} />
    </div>
  </div>
);

Project.propTypes = {
  title: PropTypes.string.isRequired,
  features: PropTypes.arrayOf(PropTypes.string).isRequired,
  instruments: PropTypes.arrayOf(PropTypes.object).isRequired,
  links: PropTypes.objectOf(PropTypes.string).isRequired,
  icons: PropTypes.arrayOf(PropTypes.string).isRequired,
};
export default Project;
