import React from 'react';
import Masonry from 'react-masonry-css';
import Project from '../components/Project';
import projects from '../data/projects-list';

const ProjectsPage = () => (
  <div name="projects" className="projects-page fullpage-centered">
    <Masonry
      breakpointCols={Math.floor(window.innerWidth / 370)}
      className="masonry-grid"
      columnClassName="masonry-grid_column"
    >
      {projects.map(({
        title, features, instruments, links, icons,
      }, i) => (
        <Project
          key={String(i)}
          title={title}
          features={features}
          instruments={instruments}
          links={links}
          icons={icons}
        />
      ))}
    </Masonry>
  </div>
);

export default ProjectsPage;
